.latestnews-section{
    width: 100%;
    margin-bottom: 30px;
    .latestnews--heading{
        margin: 16px auto 8px auto;
        h1{
            font-size: 30px;
            line-height: 148%;
            text-align: center;
            letter-spacing: 1px;
            color: #0F1214;
        }
    }
    .latestnews--content{
        margin: 40px auto;
        width: 80%;
        p{
            font-size: 16px;
            line-height: 150%;
            letter-spacing: 0.2px;
            text-align: center;
            color: #494F56;
        }
    }
    .blog__list{
        .blog__list--item{
            .blog__list--item-top{
                margin-top: 14px;
                display: flex;
                align-items: center;
                p{
                    font-size: 12px;
                    line-height: 200%;
                    letter-spacing: 0.1px;
                    color: #494F56;
                    margin: 0;
                    &:first-child{
                        margin-right: 10px;
                    }
                }
                
            }
            .blog__list--item-title{
                margin: 2px auto 12px auto;
                p{
                    font-size: 16px;
                    line-height: 150%;
                    letter-spacing: 0.2px;
                    color: #0F1214;
                }
            }
            .blog__list--item-readmore{
                p{
                    font-weight: bold;
                    font-size: 14px;
                    line-height: 189%;
                    letter-spacing: 0.4px;
                    text-decoration-line: none;
                    color: #494F56;
                    &:hover{
                        text-decoration-line: underline;
                        color: #252A2F;
                    }
                }
            }
        }
    }
}



@include desktop{
    .latestnews-section{
        .latestnews--heading{
            margin: 16px auto 8px auto;
            h1{
                font-size: 30px;
            }
        }
        .latestnews--content{
            margin: 20px auto;
            p{
                font-size: 15px;
            }
        }
        .blog__list{
            .blog__list--item{
                .blog__list--item-top{
                    margin-top: 10px;
                    display: block;
                    p{
                        font-size: 13px;
                        margin: 0;
                        &:first-child{
                            margin-right: 10px;
                        }
                    }
                }
                .blog__list--item-title{
                    margin: 2px auto 12px auto;
                    p{
                        font-size: 14px;
                    }
                }
                .blog__list--item-readmore{
                    p{
                        font-size: 13px;
                    }
                }
            }
        }
    }
}


